html {
  background-image: url('https://hdwallsource.com/img/2014/6/gray-abstract-wallpaper-22098-22654-hd-wallpapers.jpg');
  width: 100vw;
  height: 100vh;
  overflow: scroll;
  background-size: contain;
  margin: auto;
  text-align: center;
}

p {
  width: 70vw;
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
}

table {
  border-spacing: 0px 1rem;
  width: 80%;
}

td,
th {
  border-bottom: 1.5px solid black;
  /* padding: 0px; */
}

h1 {
  -ms-flex-line-pack: center;
      align-content: center;
  font-style: italic;
}

.stats {
  -ms-flex-align: center;
      align-items: center;
  height: 25px;
  width: 100px;
}

Form {
  display: -ms-flexbox;
  display: flex;
  font-size: 20px;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 300px;
  overflow: auto;
  overflow-wrap: normal;
  -ms-flex-pack: center;
      justify-content: center;
  margin-left: 150px;
}

.check {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}


